<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    entries: [],
  }),

  mounted() {
    this.entries.unshift({
      title: "Third person view",
      description: "Fixed angle third person view with mouse based camera swivel.",
      active: false,
      src: require("@/assets/thirdperson.mp4"),
    });

    this.entries.unshift({
      title: "Early alpha",
      description: "Very early alpha, showcasing block breaking/placing, player collision and velocity.",
      active: false,
      src: require("@/assets/early.mp4"),
    });

    this.entries.unshift({
      title: "Saturated textures",
      description: "More vibrant green for the grass.",
      active: false,
      src: require("@/assets/saturated.mp4"),
    });

    this.entries.unshift({
      title: "Camera tilt while walking",
      description: "Exaggerated for demonstration purposes.",
      active: false,
      src: require("@/assets/cameraTilt.mp4"),
    });

    this.entries.unshift({
      title: "Ambient Occlusion",
      description: "Dynamically darkening corners based on neighbouring voxels to achieve global Ambient occlusion, crucical for depth perception.",
      active: false,
      src: require("@/assets/equaAmbientOcclusion.mp4"),
    });

    this.entries.unshift({
      title: "Global source lights, Ambient Occlusion",
      description: "Using the Breadth-first search algorithm to propagate light values and dynamically light the scene.",
      active: false,
      src: require("@/assets/equaTestLighting.mp4"),
    });

    this.entries.unshift({
      title: "Procedurally generated volumetric clouds",
      description: "Using instanced rendering to draw thousands of overlapped spheres at variable scales, opacities and positions determined by Simplex Noise.",
      active: false,
      src: require("@/assets/clouds.mp4"),
    });

    this.entries.unshift({
      title: "Volumetric clouds in Equa",
      description: "Cloud system moved to Equa with various in game settings.",
      active: false,
      src: require("@/assets/cloudsInGame.mp4"),
    });

    this.entries.unshift({
      title: "Equa",
      description: "Demonstration of Equa's voxel engine written in Java using OpenGL.\n Full lighting system using Breadth-first search to propagate light values, sunlight shadows, procedurally generated world + more!",
      active: false,
      src: require("@/assets/fullEqua.mp4"),
    });
  },

  methods: {
    togglePlay(index) {
      let video = document.getElementById("video" + index);
      video.play();
    },
    togglePause(index) {
      let video = document.getElementById("video" + index);
      video.currentTime = 0;
      video.pause();
      this.entries[index].active = false;
    },
  },
};
</script>
